.react_scroll_deck__component {
  flex-direction: row;
  align-items: center;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  position: relative;
}

.react_scroll_deck__container {
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
  flex-flow: row;
  align-items: center;
  width: 100%;
  display: flex;
  overflow-x: scroll;
}

.react_scroll_deck__container > * {
  flex-shrink: 0;
}

.react_scroll_deck__hover_btns {
  height: full;
  pointer-events: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.react_scroll_deck__hover_btns > button {
  pointer-events: all;
  background-color: #0000;
  border: none;
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

